@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  :host {
    /* IE10+ */
    color: #151515 !important;
  }
}

:host([on="dark"]) {
  --pfe-broadcasted--text: var(--pfe-theme--color--text--on-dark, #fff);
  --pfe-broadcasted--link: var(--pfe-theme--color--link--on-dark, #73bcf7);
  --pfe-broadcasted--link--hover: var(--pfe-theme--color--link--hover--on-dark, #bee1f4);
  --pfe-broadcasted--link--focus: var(--pfe-theme--color--link--focus--on-dark, #bee1f4);
  --pfe-broadcasted--link--visited: var(--pfe-theme--color--link--visited--on-dark, #bee1f4);
  --pfe-broadcasted--link-decoration: var(--pfe-theme--link-decoration--on-dark, none);
  --pfe-broadcasted--link-decoration--hover: var(--pfe-theme--link-decoration--hover--on-dark, underline);
  --pfe-broadcasted--link-decoration--focus: var(--pfe-theme--link-decoration--focus--on-dark, underline);
  --pfe-broadcasted--link-decoration--visited: var(--pfe-theme--link-decoration--visited--on-dark, none);
}

:host([on="saturated"]) {
  --pfe-broadcasted--text: var(--pfe-theme--color--text--on-saturated, #fff);
  --pfe-broadcasted--link: var(--pfe-theme--color--link--on-saturated, #fff);
  --pfe-broadcasted--link--hover: var(--pfe-theme--color--link--hover--on-saturated, #fafafa);
  --pfe-broadcasted--link--focus: var(--pfe-theme--color--link--focus--on-saturated, #fafafa);
  --pfe-broadcasted--link--visited: var(--pfe-theme--color--link--visited--on-saturated, #d2d2d2);
  --pfe-broadcasted--link-decoration: var(--pfe-theme--link-decoration--on-saturated, underline);
  --pfe-broadcasted--link-decoration--hover: var(--pfe-theme--link-decoration--hover--on-saturated, underline);
  --pfe-broadcasted--link-decoration--focus: var(--pfe-theme--link-decoration--focus--on-saturated, underline);
  --pfe-broadcasted--link-decoration--visited: var(--pfe-theme--link-decoration--visited--on-saturated, underline);
}

:host([on="light"]) {
  --pfe-broadcasted--text: var(--pfe-theme--color--text, #151515);
  --pfe-broadcasted--link: var(--pfe-theme--color--link, #06c);
  --pfe-broadcasted--link--hover: var(--pfe-theme--color--link--hover, #004080);
  --pfe-broadcasted--link--focus: var(--pfe-theme--color--link--focus, #004080);
  --pfe-broadcasted--link--visited: var(--pfe-theme--color--link--visited, #6753ac);
  --pfe-broadcasted--link-decoration: var(--pfe-theme--link-decoration, none);
  --pfe-broadcasted--link-decoration--hover: var(--pfe-theme--link-decoration--hover, underline);
  --pfe-broadcasted--link-decoration--focus: var(--pfe-theme--link-decoration--focus, underline);
  --pfe-broadcasted--link-decoration--visited: var(--pfe-theme--link-decoration--visited, none);
}

:host {
  --context: var(--pfe-icon--context, light);
  position: relative;
  display: inline-block;
  max-width: calc(var(--pfe-icon--size, var(--pfe-theme--icon-size, 1em)) + var(--pfe-icon--Padding, 0) * 2);
  width: fit-content !important;
  max-height: calc(var(--pfe-icon--size, var(--pfe-theme--icon-size, 1em)) + var(--pfe-icon--Padding, 0) * 2);
  height: fit-content !important;
  line-height: 0;
}

:host([block]) {
  display: block;
  margin-bottom: var(--pfe-icon--spacing, var(--pfe-theme--container-spacer, 16px));
  margin-top: var(--pfe-icon--spacing, var(--pfe-theme--container-spacer, 16px));
}

:host([block]):first-child {
  margin-top: 0;
}

:host svg {
  width: var(--pfe-icon--size, var(--pfe-theme--icon-size, 1em));
  height: var(--pfe-icon--size, var(--pfe-theme--icon-size, 1em));
}

:host(:not(.load-failed)) {
  vertical-align: middle;
  border-radius: 50%;
  background-color: var(--pfe-icon--BackgroundColor, transparent);
  border: var(--pfe-icon--BorderWidth, 0) var(--pfe-theme--ui--border-style, solid) var(--pfe-icon--BorderColor, var(--pfe-icon--color, transparent));
  padding: var(--pfe-icon--Padding, 0);
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  :host(:not(.load-failed)) {
    /* IE10+ */
    background-color: white !important;
  }
  :host(:not(.load-failed)) svg filter feFlood {
    flood-color: black !important;
  }
}

@supports (-ms-accelerator: true) {
  :host(:not(.load-failed)) {
    /* Microsoft Edge Browser 12+ */
    background-color: white !important;
  }
  :host(:not(.load-failed)) svg filter feFlood {
    flood-color: black !important;
  }
}

@media screen and (-ms-high-contrast: active), screen and (-ms-high-contrast: none) {
  :host(:not(.load-failed)) svg image {
    /* IE10+ */
    filter: none;
  }
}

:host(:not(.load-failed)) filter feFlood {
  flood-color: var(--pfe-icon--Color, var(--pfe-icon--color, var(--pfe-broadcasted--text, #3c3f42)));
}

:host(:not(.load-failed)) .pfe-icon--fallback {
  display: none;
}

:host([size="2x"]) {
  --pfe-icon--size: 2em;
}

:host([size="3x"]) {
  --pfe-icon--size: 3em;
}

:host([size="4x"]) {
  --pfe-icon--size: 4em;
}

:host([size="xl"]) {
  --pfe-icon--size: 100px;
}

:host([size="lg"]) {
  --pfe-icon--size: 64px;
}

:host([size="md"]) {
  --pfe-icon--size: 32px;
}

:host([size="sm"]) {
  --pfe-icon--size: 14px;
}

:host([color="critical"]) {
  --pfe-icon--color: var(--pfe-theme--color--feedback--critical, #a30000);
  --pfe-icon--context: dark;
}

:host([color="important"]) {
  --pfe-icon--color: var(--pfe-theme--color--feedback--important, #d73401);
  --pfe-icon--context: dark;
}

:host([color="moderate"]) {
  --pfe-icon--color: var(--pfe-theme--color--feedback--moderate, #ffc024);
}

:host([color="success"]) {
  --pfe-icon--color: var(--pfe-theme--color--feedback--success, #2e7d32);
  --pfe-icon--context: dark;
}

:host([color="info"]) {
  --pfe-icon--color: var(--pfe-theme--color--feedback--info, #0277bd);
  --pfe-icon--context: dark;
}

:host([color="default"]) {
  --pfe-icon--color: var(--pfe-theme--color--feedback--default, #4f5255);
  --pfe-icon--context: dark;
}

:host([color="lightest"]) {
  --pfe-icon--color: var(--pfe-theme--color--surface--lightest, #fff);
  --pfe-icon--context: var(--pfe-theme--color--surface--lightest--context, light);
}

:host([color="base"]) {
  --pfe-icon--color: var(--pfe-theme--color--surface--base, #f0f0f0);
  --pfe-icon--context: var(--pfe-theme--color--surface--base--context, light);
}

:host([color="darker"]) {
  --pfe-icon--color: var(--pfe-theme--color--surface--darker, #3c3f42);
  --pfe-icon--context: var(--pfe-theme--color--surface--darker--context, dark);
}

:host([color="darkest"]) {
  --pfe-icon--color: var(--pfe-theme--color--surface--darkest, #151515);
  --pfe-icon--context: var(--pfe-theme--color--surface--darkest--context, dark);
}

:host([color="complement"]) {
  --pfe-icon--color: var(--pfe-theme--color--surface--complement, #002952);
  --pfe-icon--context: var(--pfe-theme--color--surface--complement--context, saturated);
}

:host([color="accent"]) {
  --pfe-icon--color: var(--pfe-theme--color--surface--accent, #004080);
  --pfe-icon--context: var(--pfe-theme--color--surface--accent--context, saturated);
}

:host([circled]:not([circled="false"])) {
  --pfe-icon--BackgroundColor: var(--pfe-icon--color, var(--pfe-theme--color--surface--lightest, #fff));
  --pfe-icon--Color:           var(--pfe-broadcasted--text, #3c3f42);
  --pfe-icon--Padding:         .5em;
  --pfe-icon--BorderWidth:     var(--pfe-theme--ui--border-width, 1px);
  --pfe-icon--BorderColor:     var(--pfe-icon--color, var(--pfe-theme--color--surface--border, #d2d2d2));
}

:host(.load-failed) svg image {
  display: none;
}

:host(.load-failed.has-fallback) svg,
:host(.load-failed[on-fail="collapse"]) svg {
  display: none;
}

:host(.load-failed[on-fail="collapse"]) {
  --pfe-icon--size: 0;
}
